body.subpage .l-header {
  opacity: 1;
  transform: translateY(0);
}

.l-header {
  z-index: 100;
  padding-inline: 40px;
  padding-top: 40px;
  position: fixed;
  width: 100%;
  opacity: 0;
  transition: transform 0.8s cubic-bezier(0.4, 0, 0.2, 1), opacity 0.8s cubic-bezier(0.4, 0, 0.2, 1);
  transition-delay: 0.8s;
  transform: translateY(-4rem);
  /* スクリーンリーダー */
}
.l-header.is-loaded {
  opacity: 1;
  transform: translateY(0);
}
.l-header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media screen and (max-width: 768px) {
  .l-header__logo {
    width: 77px;
  }
}
.l-header__nav {
  display: flex;
  align-items: center;
}
@media screen and (max-width: 1023px) {
  .l-header__nav {
    display: none;
  }
}
.l-header__menu {
  display: flex;
  align-items: center;
  flex-direction: row;
  font-family: Arial, sans-serif;
  text-transform: uppercase;
}
.l-header__menu-item {
  margin-right: 40px;
}
.l-header__menu-link {
  position: relative;
}
.l-header__menu-link:hover::before {
  transform: scaleX(1);
  transform-origin: left center;
}
.l-header__menu-link::before {
  position: absolute;
  bottom: -1.2rem;
  display: block;
  width: 100%;
  height: 1px;
  background-color: #111111;
  content: "";
  transition: transform 0.5s cubic-bezier(0.33, 1, 0.68, 1);
  transform: scaleX(0);
  transform-origin: right center;
}
.l-header__submenu {
  padding-top: 100px;
  width: 639px;
}
.l-header__submenu-inner {
  display: flex;
  flex-direction: row;
}
.l-header__submenu-image {
  width: 289px;
  height: auto;
}
.l-header__submenu-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.l-header__submenu-list {
  width: 350px;
}
.l-header__submenu-item {
  height: 33.3%;
  padding: 2em 2em 1em 2em;
}
.l-header__submenu-link {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.l-header__submenu-link:hover {
  color: #88b999;
}
.l-header__submenu-title {
  display: inline-block;
  font-family: "Noto Sans JP", sans-serif;
  font-size: 1.2rem;
}
.l-header__submenu-title .eng {
  font-family: Arial, sans-serif;
  font-size: 2.2rem;
  font-weight: bold;
  text-transform: none;
}
.l-header__hamburger-btn {
  all: unset;
  outline: revert;
  -webkit-tap-highlight-color: transparent;
  cursor: pointer;
  width: 46px;
  aspect-ratio: 1/1;
  display: grid;
  place-items: center;
  color: black;
  justify-items: start;
}
@media screen and (min-width: 1025px) {
  .l-header__hamburger-btn {
    display: none;
  }
}
@media screen and (max-width: 768px) {
  .l-header__hamburger-btn {
    width: 30px;
  }
}
.l-header__hamburger-btn-bar, .l-header__hamburger-btn::before, .l-header__hamburger-btn::after {
  content: "";
  display: block;
  width: 50%;
  height: 2px;
  background-color: currentColor;
  grid-area: 1/1;
  transition: transform 0.5s;
  text-align: left;
}
.l-header__hamburger-btn::before {
  transform: translateY(-12px);
  width: 100%;
}
.l-header__hamburger-btn::after {
  transform: translateY(12px);
  width: 25%;
}
.l-header__sr-only {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

.is-none {
  display: none;
}

.js-submenu-trigger {
  position: relative;
}

.js-submenu {
  position: absolute;
  top: 0px;
  left: -200px;
}

.open {
  position: fixed;
  overflow: hidden;
  width: 100%;
}
.open .l-header__logo {
  z-index: 200;
}
.open .l-header__hamburger-btn-bar {
  transform: scale(0);
}
.open .l-header__hamburger-btn::before {
  transform: translateY(0) rotate(45deg);
}
.open .l-header__hamburger-btn::after {
  transform: translateY(0) rotate(-45deg);
  width: 100%;
}
.open .l-header__hamburger-btn {
  z-index: 200;
}
.open .l-mobile-nav {
  pointer-events: auto;
  opacity: 1;
}

.l-mobile-nav {
  pointer-events: none;
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-header.js-view .l-header__menu {
  opacity: 1;
}
.l-header.js-hide .l-header__menu {
  opacity: 0;
  transition: opacity 0.3s linear;
}

.l-footer {
  background-color: #182f20;
  color: #ffffff;
  padding-top: 15rem;
  display: grid;
  grid-template-columns: 1fr 360px 21.6% 360px 1fr;
}
.l-footer > * {
  grid-column: 2/5;
}
.l-footer__logo {
  padding-bottom: 5rem;
}
.l-footer__address {
  grid-column: 2/2;
  line-height: 1.8em;
}
.l-footer__menu {
  grid-column: 4/4;
}
.l-footer__list {
  display: flex;
  flex-flow: row wrap;
}
.l-footer__item {
  flex-basis: 50%;
  padding: 2rem;
  padding-left: 10rem;
  width: 120px;
  text-align: left;
}
.l-footer__menu-text {
  position: relative;
  text-transform: uppercase;
  letter-spacing: 0.2rem;
  font-size: 1.6rem;
  font-family: Arial, sans-serif;
  font-weight: normal;
}
.l-footer__menu-text:before {
  content: "";
  width: 0.5rem;
  height: 0.5rem;
  background: #88b999;
  border-radius: 50%;
  position: absolute;
  top: 0.7rem;
  left: -1.2rem;
}
.l-footer__arrow {
  grid-column: 4/4;
  padding-top: 15rem;
  text-align: right;
  margin-right: 5rem;
}
.l-footer__policy {
  grid-column: 4/4;
  padding-top: 5rem;
  padding-bottom: 5rem;
  margin-top: 5rem;
  text-align: left;
  display: flex;
  font-size: 1.2rem;
  font-family: Arial, sans-serif;
  justify-content: space-between;
}
.l-footer__policy small {
  font-size: 1.2rem;
}

@media screen and (max-width: 1024px) {
  .l-footer {
    grid-template-columns: 1fr 260px 1fr 346px 1fr;
  }
}
@media (min-width: 220px) and (max-width: 768px) {
  .l-footer {
    grid-template-columns: 1fr 82% 1fr;
  }
  .l-footer > * {
    grid-column: 2/2;
  }
  .l-footer__menu {
    grid-row: 1/1;
  }
  .l-footer__logo {
    padding-top: 5rem;
  }
  .l-footer__policy {
    flex-direction: column;
    gap: 1rem;
  }
  .l-footer__item {
    padding-left: 2rem;
  }
  .l-footer__address {
    font-size: 1.2rem;
  }
}
.l-mobile-nav {
  position: fixed;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
  display: flex;
  flex-direction: column;
  gap: 22px;
  color: #111111;
  background-color: #d9e3dd;
  padding: 20% 10% 10% 10%;
}
.l-mobile-nav__menu {
  font-family: Arial, sans-serif;
  font-size: 1.8rem;
  font-weight: bold;
  text-transform: uppercase;
}
.l-mobile-nav__menu-item {
  margin-top: 1rem;
  border-bottom: 1px solid #cbcbcb;
  padding-bottom: 1rem;
}
.l-mobile-nav__submenu {
  margin-top: 1rem;
  font-size: 1.4rem;
}
.l-mobile-nav__submenu-item {
  display: flex;
  align-items: center;
  margin-top: 2rem;
  font-weight: normal;
}
.l-mobile-nav__submenu-item::before {
  border-top: 1px solid;
  content: "";
  width: 1rem; /* 線の長さ */
  margin-right: 1rem;
}
.l-mobile-nav__subtitle {
  font-family: Arial, sans-serif;
  font-size: 1.2rem;
  font-weight: normal;
  margin-bottom: 1rem;
}
.l-mobile-nav__address {
  font-size: 1.2rem;
  font-weight: normal;
  line-height: 2rem;
}
.l-mobile-nav__policy {
  font-size: 1.2rem;
  font-weight: normal;
  color: #b4b4b4;
}
.l-mobile-nav__btn-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.l-overlay {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 7;
  width: 100%;
  height: 100%;
  background-color: #fff;
  opacity: 0;
  pointer-events: none;
}

.l-hero {
  position: relative;
}
.l-hero__main-wrapper {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.l-hero__heading-wrapper {
  margin-right: -10%;
  z-index: 1;
  margin-top: 15%;
}
.l-hero__image {
  width: 73%;
  height: 604px;
}
.l-hero__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.l-hero__breadcrumbs {
  margin-top: 4rem;
  margin-right: 2.7%;
  text-align: right;
  color: #b4b4b4;
  letter-spacing: 0.3rem;
}
.l-hero__breadcrumbs a {
  margin-right: 1rem;
  text-transform: uppercase;
  text-decoration: underline;
  color: #111111;
}
.l-hero__breadcrumbs a::after {
  content: "";
  display: inline-block;
  width: 1.5rem;
  border-bottom: 1px solid;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
}
.l-hero__breadcrumbs.--service {
  position: absolute;
  bottom: -10%;
  right: 0;
  z-index: 3;
}

@media screen and (max-width: 768px) {
  .l-hero {
    display: grid;
    grid-template-columns: 2.7% 1fr 2.7%;
  }
  .l-hero > * {
    grid-column: 2/-2;
  }
  .l-hero__main-wrapper {
    display: contents;
  }
  .l-hero__heading-wrapper {
    grid-column: 2/-2;
    margin-top: 15rem;
    margin-bottom: 2rem;
  }
  .l-hero__image {
    grid-column: 1/-1;
    width: 100%;
    height: 280px;
  }
}
.l-hero--noimg {
  display: grid;
  grid-template-columns: 13.5% 1fr;
}
.l-hero--noimg__heading-wrapper {
  grid-column: 2/2;
  margin-right: -10%;
  z-index: 1;
  margin-top: 40rem;
}
.l-hero--noimg__breadcrumbs {
  grid-column: 2/2;
  margin-top: 4rem;
  margin-right: 2.7%;
  text-align: right;
  color: #b4b4b4;
  letter-spacing: 0.3rem;
}
.l-hero--noimg__breadcrumbs a {
  margin-right: 1rem;
  text-transform: uppercase;
  text-decoration: underline;
  color: #111111;
}
.l-hero--noimg__breadcrumbs a::after {
  content: "";
  display: inline-block;
  width: 1.5rem;
  border-bottom: 1px solid;
  margin-bottom: 0.5rem;
  margin-left: 0.5rem;
}
.l-hero--noimg__breadcrumbs.--service {
  position: absolute;
  bottom: -10%;
  right: 0;
  z-index: 3;
}

@media screen and (max-width: 768px) {
  .l-hero--noimg {
    display: grid;
    grid-template-columns: 2.7% 1fr 2.7%;
  }
  .l-hero--noimg > * {
    grid-column: 2/-2;
  }
  .l-hero--noimg__heading-wrapper {
    grid-column: 2/-2;
    margin-top: 15rem;
    margin-bottom: 2rem;
  }
}
.l-form {
  display: grid;
  grid-template-columns: 1fr 800px 1fr;
}
.l-form > * {
  grid-column: 2/2;
}
.l-form__info {
  padding-top: 14rem;
}
.l-form__info-desc {
  font-size: 1.5rem;
  line-height: 1.5;
  letter-spacing: 0.3rem;
  padding-bottom: 6rem;
}
.l-form__sub-heading-wrapper {
  padding-top: 11.2rem;
}
.l-form__caution {
  padding-top: 3.8rem;
  font-size: 1.5rem;
  line-height: 1.5;
  letter-spacing: 0.3rem;
}
.l-form__progress-wrapper {
  display: flex;
  justify-content: space-between;
  padding-top: 8.6rem;
}
.l-form__progress {
  width: 30%;
  text-align: center;
  font-size: 2.4rem;
  line-height: 2.5;
  letter-spacing: 0.3rem;
  background-color: #f0f0f0;
  color: #b4b4b4;
}
.l-form__progress.is-active {
  background-color: #88b999;
  color: #ffffff;
}
.l-form__form-wrapper {
  padding-top: 12.9rem;
}
.l-form__item {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-top: 8rem;
  letter-spacing: 0.3rem;
}
.l-form__dt {
  flex-basis: 30%;
  font-size: 1.8rem;
  font-weight: normal;
  letter-spacing: 0.3rem;
}
.l-form__dt.-message {
  align-self: flex-start;
  padding-top: 1rem;
}
.l-form__dd {
  flex-basis: 70%;
}
.l-form__dd.--half {
  flex-basis: 29%;
}
.l-form__dd.-company span {
  position: relative;
}
.l-form__dd.-company span:after {
  position: absolute;
  content: "選択してください";
  display: block;
  top: 7rem;
  color: #ff0000;
  letter-spacing: 0.3rem;
  width: 100%;
}
.l-form__dd.-upload {
  flex-basis: 353px;
  flex-basis: max(353px, 49%);
}
.l-form__dd.-upload span input {
  letter-spacing: 0.3rem;
}
.l-form__select {
  display: inline-block;
  background-color: #f0f0f0;
  border: 1px solid #b4b4b4;
  padding: 2rem 5rem;
}
.l-form__selectWrap {
  position: relative;
  display: block;
  width: 234px;
}
.l-form__selectWrap::after {
  position: absolute;
  right: 2.5rem;
  top: 0;
  bottom: 0;
  margin: auto;
  content: "";
