@keyframes slide {
  0% {
    -webkit-mask-size: 0% 100%;
    mask-size: 0% 100%;
  }
  100% {
    -webkit-mask-size: 100% 100%;
    mask-size: 100% 100%;
  }
}
@keyframes zoomUp {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.1);
  }
}
.swiper-wrapper {
  position: relative;
}

.swiper-slide {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  overflow: hidden;
  margin: 0 auto;
}
.swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@supports ((-webkit-mask-size : 100% 100%)) {
  .swiper-slide img {
    -webkit-mask-size: 100% 100%;
    -webkit-mask-image: url(../images/home/slider_mask.jpg);
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: left center;
  }
